body {
  background-color: white;
}
.left{
  float: left;
}
.right{
  float: right;
}
.single-wrap{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space:nowrap;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 140px;
  border-top: 3px solid #f0f2f5;
  background: white;
  z-index: 3;
}
footer table{
  width: 100%;
  height: 100%;
  font-size: 32px;
  text-align: center;
}
footer table td{
  cursor: pointer;
}
footer table img{
  width: 60px;
  margin-top: 0;
  margin-bottom: 5px;
}
footer table td:nth-of-type(2) img{
  -webkit-transform: scale(1.14);
  transform: scale(1.14);
}
footer table td:nth-of-type(3) img{
  -webkit-transform: scale(1.12);
  transform: scale(1.12);
}
html{
  height: 100%;
}
p, div{
  margin: 0;
}
img, a{
  outline: 0;
}
body{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  overflow: auto;
  -webkit-font-smoothing: antialiased;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 10px;
  -webkit-tap-highlight-color:rgba(0, 0, 0, 0);
  -moz-tap-highlight-color:rgba(0, 0, 0, 0);
  -ms-tap-highlight-color:rgba(0, 0, 0, 0);
  -o-tap-highlight-color:rgba(0, 0, 0, 0);
  tap-highlight-color:rgba(0, 0, 0, 0);
}
p{
  line-height: 100%;
  vertical-align: baseline;
  font-size: "Microsoft YaHei"!important;
}
a{
  text-decoration: none;
}
*{
  font-family: Microsoft YaHei;
}
::-webkit-scrollbar{
  display: none;
}
::-moz-selection{
  background-color: white;
  color: white;
}
::selection{
  background-color: white;
  color: white;
}
.grey{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.5;
  z-index: 4;
}
.grey-enter-active{
  -webkit-animation: show .15s;
  animation: show .15s;
}
.grey-leave-active{
  -webkit-animation: show .15s reverse;
  animation: show .15s reverse;
}
@-webkit-keyframes show {
0%{
    opacity: 0;
}
100%{
    opacity: 0.8;
}
}
@keyframes show {
0%{
    opacity: 0;
}
100%{
    opacity: 0.8;
}
}
.food-item{
  width: 100%;
  height: 340px;
  overflow: hidden;
}
.item-content{
  width: 90%;
  height: calc(100% - 3px);
  margin: 0 5%;
  z-index: 1;
}
.food-item:not(:last-of-type) .item-content{
  border-bottom: 3px solid #f0f2f5;
}
.food-item a{
  color: black;
}
.fd-pic{
  width: 240px;
  height: 240px;
  border-radius: 16px;
  background-position: 50% 50%!important;
  background-size: cover!important;
  top: 50px;
}
.fd-intro{
  width: calc(96% - 240px);
  height: 100%;
  left: 4%;
  z-index: 1;
}
.fd-pic, .fd-intro{
  position: relative;
  float: left;
}
.fd-intro p:first-of-type{
  width: 100%;
  font-size: 48px;
  font-weight: bold;
  margin-top: 54px;
}
.fd-intro p:nth-of-type(2), .fd-intro p:nth-of-type(3){
  float: left;
  font-size: 34px;
  margin-right: 22px;
  margin-top: 16px;
}
.fd-intro p:nth-of-type(4){
  clear: left;
  font-size: 46px;
  color: #FF5339;
  margin-top: 140px;
}
.cartBox{
  float: right;
  width: 12%;
  height: 120px;
  position: relative;
  bottom: 150px;
  right: calc(5% - 20px);
  z-index: 2;
}
.cartBox img{
  width: 80px;
  float: right;
  margin: 22px 11.5% 0 0;
}
